import axios from 'axios'
const server = 'http://localhost:3000'
// const server = ''

function get (url, payload) {
  url = server+url
  return axios.get(url, payload)
}
function post (url, data, payload) {
  url = server + url;
  return axios.post(url, data, payload)
}

// 
// 
export function text(id){//
  return get('/editor?id=' + id,{})
}

export function postBlog(params){//
  return post('/blog/add',params)
}







